<template>
    <div>
        <h2>수정페이지</h2>
        <input v-model="article.title"><br>
        <input v-model="article.content"><br>
        <button @click="edit">수정</button>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            article: {}
        }
    },
    created() {
        axios.get("http://localhost:9999/board/getArticle/"+this.$route.params.num).then(({data}) => this.article = data)
    },
    methods: {
        edit() {
            axios.post("http://localhost:9999/board/edit", this.article)
            this.$router.push("/article/" + this.article.num)
        }
    },
};
</script>

